*{
  margin: 0;
  padding: 0;
}
.App{
  display: grid;
  grid-template-columns: 18rem auto;
  z-index: 4;
}
.empty{
  width: 100%;
  background-color: blue;
  height: 50vh;
  position:absolute;
  z-index: -1;
  top: 0;
}
@media screen and (max-width: 1350px) {
  .App {
    grid-template-columns: 7.5rem auto;
  }
}
@media screen and (max-width:600px) {
  .App{
    display: block;
  }
}