.sidebar{
    height: 100%;
    background-color: #fff;
    margin: 1.5rem;
    margin-top: 0.5rem;
    border-radius: 1rem;
    padding: 1rem;
    box-shadow: 0px 5px 5px 5px rgba(236, 227, 227, 0.753);
    font-size: 18px;
    overflow: hidden;    
}
.profile{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
}
.dash{
    margin-top: 1rem;
    background-color: blue;
    padding: 0.5rem 0.5rem;
    border-radius: 1rem;
}
.dash>a{
    display: flex;
    text-decoration: none;
    gap: 1rem;

}
 .set{
    z-index: 2;
    position: absolute;
    display: grid;
    transition: all 0.5s;
}
.bi-x-lg {
    display: none;
  }
@media screen and (max-width: 1350px) {
    .bi-x-lg {
      display: block;
    }
  }
@media screen and (max-width:600px) {
    .sidebar{
        display: none;
    }
    .set{
        display: block;
    }

}