.list{
    display: flex;
    gap: 1rem;
    padding: 0.5rem;
}

.list>:last-child{
    margin-left: auto;
}
.content{
    margin-left: 1rem;
    display: flex;
    gap: 1rem;
    flex-direction: column;
}
.contentclose{
    display: none;
}
.list>.toggle{
    cursor: pointer;
    transition: transform 0.3s;
}
.open >.toggle{
    transform: rotate(180deg);
}
.plain{
    text-decoration: none;
    display: flex;
    gap: 1rem;
    color: black;
}
.item-list{
    padding: 0.5rem;
    margin-right: auto;
    
}