.card>:last-child{
    display: flex;
    justify-content: space-between;
    margin-top: 5px;
}
.card>:last-child>h4{
    font-weight: bold;
}
.cards{
display: grid;
grid-template-columns: repeat(4, minmax(0, 1fr));
gap: 1rem;
margin-right: 1rem;
}
.card{
    padding: 0.5rem;

}
.card>:last-child>i{
    position: relative;
    top: -1rem;
}
.cards>div>:first-child{
    text-transform: uppercase;
}
@media screen and (max-width:950px) {
    .cards{
        grid-template-columns: repeat(2, minmax(0, 1fr)); 
    }
}

@media screen and (max-width:600px) {
    .cards{
        display: block;
        margin-right: 0;
    }
    .card{
        margin: 1rem;

    }
}